<template>
  <div class="typein-header-up">
    <div class="typein-path">
      <el-input class="typein-path-input" v-model="pathSuffix" placeholder="请按约定输入图片的详细地址"
                @keyup.enter.native="handlePathSuffixConfirm" @blur="handlePathSuffixConfirm"> </el-input>
    </div>
    <div class="typein-diseaseList">
      <el-checkbox-group v-model="checkDiseaseList">
        <el-checkbox  class="typein-disease" v-for="disease in diseaseList" :label="disease.id" :key="disease.id">{{disease.className}}</el-checkbox>
      </el-checkbox-group>
      <div class="submit-button" @click="_submit" >提交</div>
    </div>
  </div>
</template>

<script>
  import Global from '../../../global/global';
  export default {
    name: "surgeon-list",
    data(){
      return{
        personId: '',
        diseaseList: [],
        checkDiseaseList: [],//存储选中的疾病的id
        pathSuffix: '',
      }
    },
    created(){
      if(this.$route.query.personId){
        this.personId = this.$route.query.personId;
      }else{
        alert("请先输入该病例个人信息数据");
        this.$router.push({path:`/typein/personalDetail`});
      }
    },
    mounted(){
      console.log('personId是',this.personId);
      //this.personId = this.$route.query.personId;
      //请求目前所有疾病大类数据
      this.$ajax({
        method: 'get',
        url: `http://${Global.serverIP}:${Global.port}/bigclass/searchAll`,
        headers: {
          'Content-Type': 'application/json',
        },
        xhrFields: {
          withCredentials: true
        },
      }).then(response=>{
        console.log(response);
        if(response.data.code === 1){
          console.log(response.data.content);
          this.diseaseList = response.data.content;
        }else if(response.data.code === 0){
          this.diseaseList = [{id:-1,
                                className:'暂无疾病大类，请联系管理员添加'}];
          this._submit = ()=>{
            this.$message("疾病大类提交按钮已注销",{})
          };
        }
        console.log('diseaseList',this.diseaseList);
      })
    },
    methods:{
      _submit(){
        const formData = this.checkDiseaseList.map((val) => +val);
        this.$ajax({
          method: 'post',
          url: `http://${Global.serverIP}:${Global.port}/waike/add/personInfo/execBigClass?personId=${this.personId}`,
          data: formData,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields: {
            withCredentials: true
          },
        }).then(response=>{
          if(response.data.code === 1){
            this.$confirm('提交成功, 是否继续录入下一位病例?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning',
            }).then(() => {
              this.$router.push({path:`/typein`});
            });
          }
        })
      },
      handlePathSuffixConfirm(){
        let cache = this.pathSuffix;
        cache.replace('\\','\\\\');
        this.$ajax({
          method: 'post',
          url: `http://${Global.serverIP}:${Global.port}/external/addFilePath?personId=${this.personId}`,
          data: cache,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields: {
            withCredentials: true
          },
        }).then(response=>{
          if(response.data.code === 1){
            this.$message('路径提交成功');
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../scss/style";

  .typein-header-up{
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    height: 1.6rem;
    font-size: 1rem;
    padding: 2rem 0;
    cursor: pointer;
    .typein-path{
      display: flex;
      align-items: center;
      padding-bottom: 2rem;
      border-bottom: 1px solid black;
      .typein-path-input{
        display: inline-block;
        width: 80%;
        margin: 0 auto;
      }
    }
    .typein-diseaseList{
      display:flex;
      flex-direction: column;
      margin: 2rem 1rem;
      .el-checkbox.typein-disease{
        margin: 0.1rem 1rem 0;
        span.el-checkbox__label{
          padding-left: 3px;
        }
      }
      .submit-button{
        @extend %submit-button;
        margin: 1rem auto;
      }
    }

  }
</style>



