import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login/Login.vue'
import Register from '@/components/Register/Register.vue'
import Typein from '@/components/Typein/Typein.vue'
import PersonalDetail from '@/sub/SubTypein/surgeonTypein/PersonalDetail.vue'
import MedicalHistory from '@/sub/SubTypein/surgeonTypein/MedicalHistory.vue'
import HealthCheck from '@/sub/SubTypein/surgeonTypein/HealthCheck.vue'
import SpecialityCheck from '@/sub/SubTypein/surgeonTypein/SpecialityCheck.vue'
import ApplianceCheck from '@/sub/SubTypein/surgeonTypein/ApplianceCheck.vue'
import CaseHistory from '@/sub/SubTypein/othodonticsTypein/CaseHistory.vue'
import Examination from '@/sub/SubTypein/othodonticsTypein/Examination.vue'
import FaceExamination from '@/sub/SubTypein/othodonticsTypein/FaceExamination.vue'
import ArthroExamination from '@/sub/SubTypein/othodonticsTypein/ArthroExamination.vue'
import UpLoadPic from '@/sub/SubTypein/UpLoadPic/UpLoadPic.vue'
import Homepage from '@/components/Homepage/Homepage.vue'
import HomepageUserInfo from '@/sub/SubHomepage/HomepageUserInfo.vue'
import HomepageDatabase from '@/sub/SubHomepage/HomepageDatabase.vue'
import HomepageRecorded from '@/sub/SubHomepage/HomepageRecorded.vue'
import HomepageOther from '@/sub/SubHomepage/HomepageOther.vue'
import PersonInfo from '@/components/PersonInfo/PersonInfo.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'FirstPage',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/register',
      name: 'Register',
      component: Register,
    },
    {
      path: '/typein',
      name: 'Typein',
      component: Typein,
      redirect: '/typein/personalDetail',
      children:[
        {
          path: 'personalDetail',
          name: 'PersonalDetail',
          component: PersonalDetail,
        },
        {
          path: 'medicalHistory',
          name: 'MedicalHistory',
          component: MedicalHistory,
        },
        {
          path: 'healthcheck',
          name: 'HealthCheck',
          component: HealthCheck,
        },
        {
          path: 'specialitycheck',
          name: 'SpecialityCheck',
          component: SpecialityCheck,
        },
        {
          path: 'appliancecheck',
          name: 'ApplianceCheck',
          component: ApplianceCheck,
        },
        {
          path: 'caseHistory',
          name: 'CaseHistory',
          component: CaseHistory,
        },
        {
          path: 'examination',
          name: 'Examination',
          component: Examination,
        },
        {
          path: 'faceExamination',
          name: 'FaceExamination',
          component: FaceExamination,
        },
        {
          path: 'arthroExamination',
          name: 'ArthroExamination',
          component: ArthroExamination,
        },
        {
          path: 'upLoadPic',
          name: 'UpLoadPic',
          component: UpLoadPic,
        }
      ]
    },
    {
      path: '/homepage/:userId',
      name: 'Homepage',
      component: Homepage,
      redirect: '/homepage/:userId/userInfo',
      children:[
        {
          path: 'userInfo',
          name: 'HomepageUserInfo',
          component: HomepageUserInfo,
        },
        {
          path: 'recorded',
          name: 'HomepageRecorded',
          component: HomepageRecorded,
        },
        {
          path: 'database',
          name: 'HomepageDatabase',
          component: HomepageDatabase,
        },
        {
          path: 'other',
          name: 'HomepageOther',
          component: HomepageOther,
       }
        ]
       },
    {
      path: '/personInfo/:personId',
      name: 'PersonInfo',
      component: PersonInfo
    }
  ]
})



