<template>
  <div class="person-detail-wrap">
    <el-tag>牙合情况</el-tag>
    <input-box :title="tagTitleFirst" id="ruyahe" v-model="postData.ruyahe"> </input-box>
    <input-box :title="tagTitleSecond" id="henyahe" v-model="postData.henyahe"> </input-box>
    <el-tag>牙槽突</el-tag>
    <input-box :title="tagTitleThird" id="up_tooth" v-model="postData.up_tooth"> </input-box>
    <input-box :title="tagTitleFourth" id="down_tooth" v-model="postData.down_tooth"> </input-box>
    <div class="submit-button" @click="_submit" >提交，进入下一页</div>
  </div>
</template>

<script>
  import InputBox from '../../../assets/inputBox/inputBox'
  import Global from '../../../global/global';
  export default {
    name: "examination",
    data(){
      return{
        tagTitleFirst: "乳牙合情况（早失 龋齿  滞留）",
        tagTitleSecond: "恒牙合情况（早失 龋齿  隐裂牙 松动度 牙龈萎缩情况 牙周袋 其他）",
        tagTitleThird: "上牙弓（丰满 欠丰满 凹陷）",
        tagTitleFourth: "牙弓（丰满 欠丰满 凹陷）",
        postData: {
          ruyahe:'',
          henyahe:'',
          up_tooth:'',
          down_tooth:'',
        },
        personId: '',
      };
    },
    components:{
      InputBox,
    },
    created(){
      if(this.$route.query.personId){
        this.personId = this.$route.query.personId;
      }else{
        alert("请先输入该病例个人信息数据");
        this.$router.push({path:`/typein`});
      }
    },
    methods:{
      _submit(event) {
        event.preventDefault();
        const formData = JSON.stringify(this.postData);
        this.$ajax({
          method: 'post',
          url: `http://${Global.serverIP}:${Global.port}/zhenji/add/${this.personId}/faceBedCheck`,
          data: formData,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields:{
            withCredentials:true
          },
        }).then((response) => {
          console.log(response);
          this.$router.push({path:`/typein/faceExamination?personId=${this.personId}`});
        })
      }
    }

  }
</script>


<style scoped lang="scss">
  @import "../../../scss/style";

  .submit-button{
    @extend %submit-button;
  }
</style>



