<template>
  <div class="person-detail-wrap">
    <input-box :title="tagTitleFirst" id="openType" v-model="postData.openType"> </input-box>
    <input-box :title="tagTitleSecond" id="openAngle" v-model="postData.openAngle"> </input-box>
    <input-box :title="tagTitleThird" id="snap" v-model="postData.snap"> </input-box>
    <input-box :title="tagTitleFourth" id="ache" v-model="postData.ache"> </input-box>
    <div class="submit-button" @click="_submit" >提交，进入下一页</div>
  </div>
</template>

<script>
  import InputBox from '../../../assets/inputBox/inputBox'
  import Global from '../../../global/global';
  export default {
    name: "arthro-examination",
    data(){
      return{
        tagTitleFirst: "开口型",
        tagTitleSecond: "开口度",
        tagTitleThird: "弹响",
        tagTitleFourth: "疼痛",
        postData: {
          openType:'',
          openAngle:'',
          snap:'',
          ache:'',
        },
        personId: '',
      };
    },
    components:{
      InputBox,
    },
    created(){
      if(this.$route.query.personId){
        this.personId = this.$route.query.personId;
      }else{
        alert("请先输入该病例个人信息数据");
        this.$router.push({path:`/typein`});
      }
    },
    methods:{
      _submit(event) {
        event.preventDefault();
        const formData = JSON.stringify(this.postData);
        this.$ajax({
          method: 'post',
          url:`http://${Global.serverIP}:${Global.port}/zhenji/add/${this.personId}/jointCheck`,
          data: formData,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields:{
            withCredentials:true
          },
        }).then((response) => {
          console.log(response);
          this.$router.push({path:`/typein/upLoadPic?personId=${this.personId}`});
        })

      }
    }

  }
</script>


<style scoped lang="scss">
  @import "../../../scss/style";

  .submit-button{
    @extend %submit-button;
  }
</style>




