import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios'
import Global from './global/global';
Vue.use(Vuex);


const UserInfoCache = localStorage.getItem('MedicineDatabaseUserInfo') || JSON.stringify({});
export default new Vuex.Store({
  state: {
    userInfo: JSON.parse(UserInfoCache),
    personId: 0,
    thisPageData: [],
    roleId: 2,
  },
  mutations: {
    setUserInfo(state, userInfo) {
      userInfo && localStorage.setItem('MedicineDatabaseUserInfo',JSON.stringify(userInfo));
      state.userInfo = userInfo;
    },
    setPersonId(state, personId) {
      state.personId = personId;
    },
    setThisPageData(state, thisPageData) {
      state.thisPageData = thisPageData;
    }
  },
  actions: {
    checkLoginStatus({commit, state}) {
      const cache = JSON.parse(localStorage.getItem('MedicineDatabaseUserInfo'));
      cache && commit('setUserInfo', cache);
      return axios.get(`http://${Global.serverIP}:${Global.port}/login_check`).then((response) => {
        // 登陆状态检查
          return response.data.content;
      }).catch(() => {
        // 登陆状态清空
      })
    }
  }
})



