<template>
  <div class="homepage-wrap">
    <el-tabs v-model="activeName" @tab-click="handleClick" type="card">
      <el-tab-pane label="用户管理"  name="userInfo"> </el-tab-pane>
      <el-tab-pane label="数据库总览"  v-if="adminIsShow" name="database"> </el-tab-pane>
      <el-tab-pane label="已录入数据" name="recorded"> </el-tab-pane>
      <el-tab-pane label="个人设置" name="other"> </el-tab-pane>
    </el-tabs>
    <div class="underline"></div>
    <div class="homepage-content">
      <router-view> </router-view>
    </div>
    <div class="button-to-typein">
      <el-button type="primary" icon="el-icon-edit" @click="buttonToTypein">录数据</el-button>
    </div>
  </div>
</template>

<script>
  import Global from '../../global/global';

  export default {
    name: "homepage",
    data() {
      return {
        activeName: 'first',
        userId: 0,
        roleId: 2,
        adminIsShow: false,
      }
    },
    created() {
      this.userId = this.$route.params.userId;
      let cache = JSON.parse(localStorage.getItem('MedicineDatabaseUserInfo'));
      this.roleId = cache.roleId;
      if(this.roleId === 0){
        this.adminIsShow = true;
      }
    },
    methods: {
      handleClick(tab, event) {
        this.$router.push({path: `/homepage/${this.userId}/${this.activeName}`});

      },
      buttonToTypein(){
        this.$router.push({path: `/typein/personalDetail`});
      }
    }

  }
</script>

<style scoped lang="scss">
  @import "../../scss/font";
  @import "../../scss/style";

  * {
    margin: 0;
    border: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  .homepage-wrap {
    position: relative;
    overflow: auto;
    min-height: 60%;
    max-height: 99vh;
    max-width: 1080px;
    min-width: 960px;
/*    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);*/
    background-color: #E8E8E8;
    -webkit-border-radius: $border-radius-big;
    -moz-border-radius: $border-radius-big;
    border-radius: $border-radius-big;
    padding: 1rem;
    .underline {
      border-bottom: 3px solid #10adff;
      position: absolute;
      bottom: 0;
      width: 0;
    }
    .homepage-content {
    }
    .button-to-typein {
      position: fixed;
      top: 1rem;
      right: 1rem;
    }
  }
</style>



