<template>
  <div class="homepage-userInfo">
    <div class="homepage-username">用户名：{{this.username}}</div>
    <div class="homepage-recoded-number">已录入病例数量：{{this.recordedNumber}}</div>
    <div class="homepage-authority">用户等级：{{authorities[this.roleId]}}</div>
  </div>
</template>

<script>
  import Global from '../../global/global';

  export default {
    name: "homepage-user-info",
    data() {
      return {
        userId: 0,
        username: "",
        recordedNumber: 0,
        thisPageData: [],
        thisPageNumber: 1,
        authorities: ["管理员","普通用户","游客"],
        roleId: 2,
      }
    },
    mounted() {
      //this.userId = this.$store.state.userInfo.userId;
      this.userId = this.$route.params.userId;
      this.roleId = this.$store.state.userInfo.roleId;
      const userInfo = Object.assign({}, this.$store.state.userInfo);
      console.log("下面是userId:");
      console.log(this.userId);
      //获取该用户已录入病例个数
      this.$ajax({
        method: 'get',
        url: `http://${Global.serverIP}:${Global.port}/search/patients_count?userId=${this.userId}`,
        headers: {
          'Content-Type': 'application/json',
        }
      }).then((response) => {
        console.log(response);
        if (response.data.code === 1) {
          this.username = response.data.content.userName;
          this.recordedNumber = response.data.content.recordedNumber;
          userInfo.username = this.username;
          userInfo.recordedNumber = this.recordedNumber;
          userInfo.userId = this.userId;
          this.$store.commit("setUserInfo", userInfo);
        }
      });

      //获取该用户已录入病例的简单清单
      // this.$ajax({
      //   method: 'get',
      //   url: `http://${Global.serverIP}:${Global.port}/search/patients?userId=${this.userId}&pageSize=10&pageNum=${this.thisPageNumber}`,
      //   headers: {
      //     'Content-Type': 'application/json',
      //   }
      // }).then((response) => {
      //   if (response.data.code === 1) {
      //     this.thisPageData = response.data.content;
      //     this.$store.commit("setThisPageData", this.thisPageData);
      //   }
      // });
    }
  }
</script>

<style lang="scss">
  @import "../../scss/font";
  @import "../../scss/style";
.homepage-userInfo{
  padding: 1rem 1rem;
  .homepage-username {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }

  .homepage-recoded-number {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
  .homepage-authority{
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
}
</style>



