<template>
  <div class="input-wrap">
    <h2 class="input-title">{{title}}</h2>
    <div class="errorInfo"></div>
    <div class="input-form">
      <input class="input-form-content" type="text" @input="inputFunc" :value="value">
      <div class="unit">{{unit}}</div>
      <div class="input-form-error">{{errorMessage}}</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "input-box",
    props: {
      title: {
        type: String,
        default: '',
      },
      id: {
        type: String,
        default: '',
      },
      unit: {
        type: String,
        default: '',
      },
      value: {
        type: String,
        default: ''
      },
      validators: {
        //传入包含validator的数组，验证每个函数判断是否符合要求。
        type: Array,
        default: () => []
      }
    },

    data() {
      return {
        oValue: '',
        errorMessage: ''
      }
    },
    methods: {
      inputFunc(evt) {
        const value = evt.target.value,
          validatorLength = this.validators.length;
        let errorMessage = '';

        for (let i = 0; i < validatorLength; i++) {
          const validator = this.validators[i] || (() => null),
                validatorErrorMessage = validator(value);
          if (validatorErrorMessage) {
            errorMessage = validatorErrorMessage;
            break;
          }
        }
        this.errorMessage = errorMessage;

        this.$emit('input', evt.target.value);
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../scss/font.scss";
  @import "../../scss/style.scss";

  .input-wrap {
    margin: 1.1rem 0;
    .input-title {
      margin-bottom: 0.3rem;
    }
    .input-form {
      font-size: $font-size-normal;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      input {
        @extend %typein-input;
      }
      .unit {
        margin-left: 1rem;
      }
    }
  }
</style>



