<template>
  <div class="person-detail-wrap">
    <el-tag>正 面</el-tag>
    <input-box :title="tagTitleFirst" id="faceShapeFront" v-model="postData.faceShapeFront"> </input-box>
    <input-box :title="tagTitleSecond" id="faceSymmetry" v-model="postData.faceSymmetry"> </input-box>
    <input-box :title="tagTitleThird" id="faceConvexity" v-model="postData.faceConvexity"> </input-box>
    <input-box :title="tagTitleFourth" id="lips" v-model="postData.lips"> </input-box>
    <el-tag>侧 面</el-tag>
    <input-box :title="tagTitleFifth" id="faceShapeSide" v-model="postData.faceShapeSide"> </input-box>
    <input-box :title="tagTitleSixth" id="nasolabialAngle" v-model="postData.nasolabialAngle"> </input-box>
    <input-box :title="tagTitleSeventh" id="jaw" v-model="postData.jaw"> </input-box>
    <input-box :title="tagTitleEighth" id="softTissue" v-model="postData.softTissue"> </input-box>
    <div class="submit-button" @click="_submit" >提交，进入下一页</div>
  </div>
</template>

<script>
  import InputBox from '../../../assets/inputBox/inputBox'
  import Global from '../../../global/global';
  export default {
    name: "face-examination",
    data(){
      return{
        tagTitleFirst: "面型",
        tagTitleSecond: "面部对称性（颏中线 上下牙列中线 咬合平面 口角）",
        tagTitleThird: "面中间1/3凸度",
        tagTitleFourth: "口唇（开唇露齿 颏唇沟 上下唇）",
        tagTitleFifth: "面型",
        tagTitleSixth: "鼻唇角",
        tagTitleSeventh: "颌骨（上下颌）",
        tagTitleEighth: "软组织",
        postData: {
          faceShapeFront:'',
          faceSymmetry:'',
          faceConvexity:'',
          lips:'',
          faceShapeSide:'',
          nasolabialAngle:'',
          jaw:'',
          softTissue:'',
        },
        personId: '',
      };
    },
    components:{
      InputBox,
    },
    created(){
      if(this.$route.query.personId){
        this.personId = this.$route.query.personId;
      }else{
        alert("请先输入该病例个人信息数据");
        this.$router.push({path:`/typein`});
      }
    },
    methods:{
      _submit(event) {
        event.preventDefault();
        const formData = JSON.stringify(this.postData);
        this.$ajax({
          method: 'post',
          url: `http://${Global.serverIP}:${Global.port}/zhenji/add/${this.personId}/faceCheck`,
          data: formData,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields:{
            withCredentials:true
          },
        }).then((response) => {
          console.log(response);
          this.$router.push({path:`/typein/arthroExamination?personId=${this.personId}`});
        })

      }
    }

  }
</script>


<style scoped lang="scss">
  @import "../../../scss/style";

  .submit-button{
    @extend %submit-button;
  }
</style>




