<template>
  <div class="homepage-recorded-data-wrap">
    <el-table :data="thisPageData" stripe style="width: 100%" height="80vh">
      <el-table-column prop="personName" label="姓名" width="180"> </el-table-column>
      <el-table-column prop="personSex" label="性别" width="180"> </el-table-column>
      <el-table-column prop="patientId" label="病人Id"> </el-table-column>
      <el-table-column label="操作" >
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button v-if="adminIsShow"
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      class="pagination"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="10"
      layout="prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>
  import Global from '../../global/global';

  export default {
    name: "homepage-recorded",
    data() {
      return {
        userId: 0,
        roleId: 2,
        adminIsShow: false,
        username: "",
        recordedNumber: 0,
        thisPageData: [],
        thisPageNumber: 1,
        total: 0,
        currentPage: 1,
      }
    },
    created (){
      this.userId = this.$store.state.userInfo.userId;
      this.thisPageData = this.$store.state.thisPageData;
      this.total = this.$store.state.userInfo.recordedNumber;
      //获取该用户已录入病例的简单清单
      this.$ajax({
        method: 'get',
        url: `http://${Global.serverIP}:${Global.port}/search/patients?userId=${this.userId}&pageSize=10&pageNum=${this.thisPageNumber}`,
        headers: {
          'Content-Type': 'application/json',
        }
      }).then((response) => {
        if (response.data.code === 1) {
          this.thisPageData = response.data.content.personIdArray;
          this.$store.commit("setThisPageData", this.thisPageData);
        }
      });
      //localstorage取roleId
      let cache = JSON.parse(localStorage.getItem('MedicineDatabaseUserInfo'));
      this.roleId = cache.roleId;
      if(this.roleId === 0){
        this.adminIsShow = true;
      }
    },
    methods:{
      handleCurrentChange(newPage){
        this.$ajax({
          method: 'get',
          url: `http://${Global.serverIP}:${Global.port}/search/patients?userId=${this.userId}&pageSize=10&pageNum=${newPage}`,
          headers: {
            'Content-Type': 'application/json',
          }
        }).then((response) => {
          if (response.data.code === 1) {
            this.thisPageData = response.data.content.personIdArray;
            this.$store.commit("setThisPageData", this.thisPageData);
            this.currentPage = newPage;
          }
        });
      },
      // rowClick(row, event, column){
      //   this.$router.push({path:`/personInfo/${row.personId}`});
      // },
      handleEdit(index,row){
        this.$router.push({path:`/personInfo/${row.personId}`});
      },
      handleDelete(index,row){
        this.$confirm("确认删除操作吗","提示",{
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(()=>{
          //删除该病例的请求。
          this.$ajax({
            method: 'get',
            url: `http://${Global.serverIP}:${Global.port}/waike/deleteOne?personId=${row.personId}`,
            headers: {
              'Content-Type': 'application/json',
            }
          }).then(response=>{
            if(response.data.code === 1){
              this.$message("删除成功");
              this.handleCurrentChange(this.currentPage);
            }else if(response.data.code === 0 ){
              this.$message(response.data.msg);
            }
          })
        });
      },
    }
  }
</script>

<style lang="scss">
.pagination{
  text-align:center;
  margin: 1rem auto 0;
}
</style>



