<template>
  <div class="person-detail-wrap">
    <input-box :title="tagTitleFirst" id="personName" v-model="postData.personName"></input-box>
    <input-box :title="tagTitleSecond" id="patientId" v-model="postData.patientId"></input-box>
    <input-box :title="tagTitleThird" id="personSex" v-model="postData.personSex"></input-box>
    <input-box :title="tagTitleFourth" id="personAge" v-model="postData.personAge" unit="岁"></input-box>
    <input-box :title="tagTitleFifth" id="personMarriage" v-model="postData.personMarriage"></input-box>
    <input-box :title="tagTitleSixth" id="personBirthplace" v-model="postData.personBirthplace"></input-box>
    <input-box :title="tagTitleSeventh" id="personNation" v-model="postData.personNation"></input-box>
    <input-box :title="tagTitleEighth" id="personCareer" v-model="postData.personCareer"></input-box>
    <input-box :title="tagTitleNinth" id="personWorkplace" v-model="postData.personWorkplace"></input-box>
    <input-box :title="tagTitleTenth" id="personHome" v-model="postData.personHome"></input-box>
    <input-box :title="tagTitleEleventh" id="personSpeaker" v-model="postData.personSpeaker"></input-box>
    <input-box :title="tagTitleTwelfth" id="personIn" v-model="postData.personIn"></input-box>
    <input-box :title="tagTitleThirteenth" id="personOut" v-model="postData.personOut"></input-box>
    <input-box :title="tagTitleFourteenth" id="personText" v-model="postData.personText"></input-box>
    <div class="submit-button" @click="_submit">提交，进入下一页</div>
  </div>
</template>

<script>
  import InputBox from '../../../assets/inputBox/inputBox'
  import Global from '../../../global/global';

  export default {
    name: "personal-detail",
    data() {
      return {
        personId: "",
        tagTitleFirst: "姓名",
        tagTitleSecond: "ID",
        tagTitleThird: "性別",
        tagTitleFourth: "年龄",
        tagTitleFifth: "婚姻",
        tagTitleSixth: "出生地",
        tagTitleSeventh: "民族",
        tagTitleEighth: "职业",
        tagTitleNinth: "工作单位",
        tagTitleTenth: "住址",
        tagTitleEleventh: "供史者",
        tagTitleTwelfth: "入院时间",
        tagTitleThirteenth: "记录时间",
        tagTitleFourteenth: "主诉",
        postData: {
          personName: "",
          patientId: "",
          personSex: "",
          personAge: "",
          personMarriage: "",
          personBirthplace: "",
          personNation: "",
          personCareer: "",
          personWorkplace: "",
          personHome: "",
          personSpeaker: "",
          personIn: "",
          personOut: "",
          personText: ""
        },
      };
    },
    components: {
      InputBox,
    },
    created() {
      if (this.$route.query.personId) {
        console.log(this.$route.query.personId);
        this.personId = this.$route.query.personId;
        this.$ajax({
          method: 'get',
          url: `http://${Global.serverIP}:${Global.port}/search/patient/detail/?personId=${this.personId}`,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields: {
            withCredentials: true
          },
        }).then((response) => {
            console.log(response);
            let aResponse = response.data.content;
            if (Array.isArray(aResponse)) {
              this.postData.personName = aResponse[0].personName;
              this.postData.patientId = aResponse[0].patientId;
              this.postData.personSex = aResponse[0].personSex;
              this.postData.personAge = aResponse[0].personAge;
              this.postData.personMarriage = aResponse[0].personMarriage;
              this.postData.personBirthplace = aResponse[0].personBirthplace;
              this.postData.personNation = aResponse[0].personNation;
              this.postData.personCareer = aResponse[0].personCareer;
              this.postData.personWorkplace = aResponse[0].personWorkplace;
              this.postData.personHome = aResponse[0].personHome;
              this.postData.personSpeaker = aResponse[0].personSpeaker;
              this.postData.personIn = aResponse[0].personIn;
              this.postData.personOut = aResponse[0].personOut;
              this.postData.personText = aResponse[0].personText;
            }
          }
        )
      }
    },
    methods: {
      _submit(event) {
        console.log("!!!!",this.personId);
        event.preventDefault();
        const formData = JSON.stringify(this.postData);
        console.log(formData);
        this.$ajax({
          method: 'post',
          url: `http://${Global.serverIP}:${Global.port}/waike/add/personInfo`,
          data: formData,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields: {
            withCredentials: true
          },
        }).then((response) => {
          console.log("病例个人信息页面提交的response");
          console.log(response);
          this.personId = response.data.content.personId;
          this.$store.commit('setPersonId', this.personId);
          this.$router.push({path: `/typein/medicalHistory?personId=${this.personId}`});
        })
      }
    }
  }
</script>


<style scoped lang="scss">
  @import "../../../scss/style";

  .submit-button {
    @extend %submit-button;
  }
</style>



