<template>
  <div class="person-detail-wrap">
    <input-box :title="tagTitleFirst" id="temperature" v-model="postData.temperature"> </input-box>
    <input-box :title="tagTitleSecond" id="pulse" v-model="postData.pulse"> </input-box>
    <input-box :title="tagTitleThird" id="breathe" v-model="postData.breathe"> </input-box>
    <input-box :title="tagTitleFourth" id="bloodPressure" v-model="postData.bloodPressure"> </input-box>
    <input-box :title="tagTitleFifth" id="height" v-model="postData.height"> </input-box>
    <input-box :title="tagTitleSixth" id="weight" v-model="postData.weight"> </input-box>
    <input-box :title="tagTitleSeventh" id="generalCase" v-model="postData.generalCase"> </input-box>
    <input-box :title="tagTitleEighth" id="skin" v-model="postData.skin"> </input-box>
    <input-box :title="tagTitleNinth" id="lymphGland" v-model="postData.lymphGland"> </input-box>
    <input-box :title="tagTitleTenth" id="headOrgan" v-model="postData.headOrgan"> </input-box>
    <input-box :title="tagTitleEleventh" id="eyes" v-model="postData.eyes"> </input-box>
    <input-box :title="tagTitleTwelfth" id="ear" v-model="postData.ear"> </input-box>
    <input-box :title="tagTitleThirteenth" id="nose" v-model="postData.nose"> </input-box>
    <input-box :title="tagTitleFourteenth" id="mouth" v-model="postData.mouth"> </input-box>
    <input-box :title="tagTitleFifteenth" id="neck" v-model="postData.neck"> </input-box>
    <input-box :title="tagTitleSixteenth" id="chest" v-model="postData.chest"> </input-box>
    <input-box :title="tagTitleSeventeenth" id="lung" v-model="postData.lung"> </input-box>
    <input-box :title="tagTitleEighteenth" id="heart" v-model="postData.heart"> </input-box>
    <input-box :title="tagTitleNineteenth" id="artery" v-model="postData.artery"> </input-box>
    <input-box :title="tagTitleTwentieth" id="around" v-model="postData.around"> </input-box>
    <input-box :title="tagTitleTwentyFirst" id="belly" v-model="postData.belly"> </input-box>
    <input-box :title="tagTitleTwentySecond" id="door" v-model="postData.door"> </input-box>
    <input-box :title="tagTitleTwentyThird" id="spine" v-model="postData.spine"> </input-box>
    <input-box :title="tagTitleTwentyFourth" id="limbs" v-model="postData.limbs"> </input-box>
    <input-box :title="tagTitleTwentyFifth" id="reflect" v-model="postData.reflect"> </input-box>
    <div class="submit-button" @click="_submit" >提交，进入下一页</div>
  </div>
</template>

<script>
  import InputBox from '../../../assets/inputBox/inputBox'
  import Global from '../../../global/global';
  export default {
    name: "personal-detail",
    data(){
      return{
        tagTitleFirst: "体温",
        tagTitleSecond: "脉搏",
        tagTitleThird: "呼吸",
        tagTitleFourth: "血压",
        tagTitleFifth: "身高",
        tagTitleSixth: "体重",
        tagTitleSeventh: "一般情况",
        tagTitleEighth: "皮肤、粘膜",
        tagTitleNinth: "淋巴结",
        tagTitleTenth: "头部及其器官",
        tagTitleEleventh: "眼",
        tagTitleTwelfth: "耳",
        tagTitleThirteenth: "鼻",
        tagTitleFourteenth: "口腔",
        tagTitleFifteenth: "颈部",
        tagTitleSixteenth: "胸部",
        tagTitleSeventeenth: "肺部(视触叩听)",
        tagTitleEighteenth: "心脏(视触叩听)",
        tagTitleNineteenth: "桡动脉",
        tagTitleTwentieth: "周围血管征",
        tagTitleTwentyFirst: "腹部（视触叩听）",
        tagTitleTwentySecond: "肛门、直肠及外生殖器",
        tagTitleTwentyThird: "脊柱",
        tagTitleTwentyFourth: "四肢",
        tagTitleTwentyFifth: "神经反射",
        postData: {
           temperature: "",
           pulse: "",
           breathe: "",
           bloodPressure: "",
           height: "",
           weight: "",
           generalCase: "",
           skin: "",
           lymphGland: "",
           headOrgan: "",
           eyes: "",
           ear: "",
           nose: "",
           mouth: "",
           neck: "",
           chest: "",
           lung: "",
           heart: "",
           artery: "",
           around: "",
           belly: "",
           door: "",
           spine: "",
           limbs: "",
           reflect: ""
        },
        personId: '',
      };
    },
    components:{
      InputBox,
    },
    created(){
      if(this.$route.query.personId){
        this.personId = this.$route.query.personId;
      }else{
        alert("请先输入该病例个人信息数据");
        this.$router.push({path:`/typein`});
      }
    },
    methods:{
      _submit(event) {
        event.preventDefault();
        const formData = JSON.stringify(this.postData);
        this.$ajax({
          method: 'post',
          url: `http://${Global.serverIP}:${Global.port}/waike/add/${this.personId}/healthInfo`,
          data: formData,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields:{
            withCredentials:true
          },
        }).then((response) => {
          console.log(response);
          this.$router.push({path:`/typein/specialitycheck?personId=${this.personId}`});
        })

      }
    }

  }
</script>


<style scoped lang="scss">
  @import "../../../scss/style";

  .submit-button{
    @extend %submit-button;
  }
</style>



