<template>
  <div class="register">
    <div class="register-title"><p>医学信号数据库系统</p></div>
    <div class="register-wrap">
      <div class="register-header">注 册</div>
      <div class="register-body">
        <form>
          <div class="form-group">
            <label>用户名：</label>
            <input type="text" class="form-control" name="username" ref="username" required="true" placeholder="请输入您的用户名" />
          </div>
          <div class="form-group">
            <label>密码：</label>
            <input type="password" class="form-control" name="password" ref="password" required="true" placeholder="请输入您的密码" />
          </div>
          <div class="form-group">
            <label>重新输入密码：</label>
            <input type="password" class="form-control" name="password" ref="password2" required="true" placeholder="请再次输入您的密码"  />
          </div>
          <div class="form-group clearfix">
            <button class="submit-register" @click="_register">注册</button>
          </div>
        </form>
        <div class="register-body-bottom">
          <router-link to="/login">
            <div class="register-body-register"><a>已有账号，去登陆</a></div>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Global from '../../global/global';

  export default {
    name: "register",
    data(){
      return{
        flag: false,//判断条件是否都满足了，是否可以点击注册
        username: "",
        password: "",
        password2: ""
      }
    },
    methods:{
      _register:function(event){
        event.preventDefault();
        let that = this;
        that.username = that.$refs.username.value;
        that.password = that.$refs.password.value;
        that.password2 = that.$refs.password2.value;
        this.$ajax({
          method: 'post',
          url: 'http://' + Global.serverIP + ':' + Global.port + '/register',
          data: {
            userName: that.username,
            passWord: that.password,
            passWord2: that.password2
          },
          headers:{
            'Content-Type':'application/json',
          },
          xhrFields:{
            withCredentials:true
          },
        }).then(function(response){
          if(response.data.code === 1){
            let con = confirm("注册成功，去登陆");
            if(con){
              that.$router.push({path:'/login'});
            }
          }else if(response.data.code === 0){
            alert("该账号已存在，请重新注册");
          }
        });
      }
    }
  }
</script>

<style lang="scss">
  @import "../../scss/font";
  @import "../../scss/style";
  * {
    margin: 0;
    border: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  .register {
    position: fixed;
    min-height: 352px;
    min-width: 352px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    background-color: #E8E8E8;
    -webkit-border-radius: $border-radius-big;
    -moz-border-radius: $border-radius-big;
    border-radius: $border-radius-big;
    .register-title {
      margin: $box-padding auto 10px;
      p {
        font-size: $font-size-title;
        font-weight: bold;
        line-height: $font-size-title;
        text-align: center;
      }
    }
    .register-wrap {
      padding: $box-padding;
      .register-header{
        font-size: $font-size-subtitle;
        margin-bottom: 10px;
        font-weight: bold;
      }
      .register-body{
        font-size:$font-size-normal;
        form{
          .form-group{
            margin-bottom: 16px;
            label{
              display: inline-block;
              max-width: 100%;
              margin-bottom: 8px;
            }
            input{
              @extend %input;
            }
            .submit-register{
              float: right;
              @extend %submit-button;
            }
          }
        }
        .register-body-bottom{
          display: flex;
          justify-content: flex-end;
          .register-body-register{
            cursor: pointer;
            a{
              text-decoration: none;
              color: black;
              &:hover {
                color: red;
                text-decoration: underline;
              }
            }
          }
        }
      }
    }
  }
</style>



