<template>
  <div class="typein-header-second-level">
    <router-link :to="`/typein/caseHistory${personIdSuffix}`"
                 class="typein-header-second-level-sub">病史
    </router-link>
    <router-link :to="`/typein/examination${personIdSuffix}`"
                 class="typein-header-second-level-sub">临床检查
    </router-link>
    <router-link :to="`/typein/faceExamination${personIdSuffix}`"
                 class="typein-header-second-level-sub">面部检查
    </router-link>
    <router-link :to="`/typein/arthroExamination${personIdSuffix}`"
                 class="typein-header-second-level-sub">关节检查
    </router-link>
  </div>
</template>

<script>
  import Global from '../../global/global';
  export default {
    name: "orthodontics-list",
    computed: {
      personIdSuffix() {
        const personId = this.$route.query.personId;
        return personId ? `?personId=${personId}` : '';
      }
    }
  }
</script>

<style lang="scss">
  .typein-header-second-level {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    height: 1.6rem;
    font-size: 1rem;
    padding: 0.3rem 0;
    cursor: pointer;
    a {
      text-decoration: none;
      color: black;
    }
    .router-link-active {
      text-decoration: none;
      border-bottom: 1px solid black;
      /**
      ！未完成 激活后的样式
       */
    }
    .typein-header-second-level-sub {
      flex: 1;
      text-align: center;
      padding: 0.3rem 0;
    }
  }
</style>



