<template>
  <div class="person-detail-wrap">
    <input-box :title="tagTitleFirst" id="mouthOutside" v-model="postData.mouthOutside"></input-box>
    <input-box :title="tagTitleSecond" id="mouthInside" v-model="postData.mouthInside"></input-box>
    <div class="submit-button" @click="_submit">提交，进入下一页</div>
  </div>
</template>

<script>
  import InputBox from '../../../assets/inputBox/inputBox'
  import Global from '../../../global/global';

  export default {
    name: "personal-detail",
    data() {
      return {
        tagTitleFirst: "口外",
        tagTitleSecond: "口内",
        postData: {
          mouthOutside: "",
          mouthInside: "",
        },
        personId: '',
      };
    },
    components: {
      InputBox,
    },
    created() {
      if (this.$route.query.personId) {
        this.personId = this.$route.query.personId;
      } else {
        alert("请先输入该病例个人信息数据");
        this.$router.push({path: `/typein`});
      }
    },
    methods: {
      _submit(event) {
        event.preventDefault();
        const formData = JSON.stringify(this.postData);
        this.$ajax({
          method: 'post',
          url: `http://${Global.serverIP}:${Global.port}/waike/add/${this.personId}/specialityCheckup`,
          data: formData,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields:{
            withCredentials:true
          },
        }).then((response) => {
          console.log(response);
          this.$router.push({path:`/typein/appliancecheck?personId=${this.personId}`});
        })

      }
    }

  }
</script>


<style scoped lang="scss">
  @import "../../../scss/style";

  .submit-button {
    @extend %submit-button;
  }
</style>



