<template>
  <div class="typein-wrap">
    <ul class="typein-header-first-level">
      <li class="typein-header-first-level-sub" @click="tabChange(SurgeonList)">外 科</li>
      <li class="typein-header-first-level-sub" @click="tabChange(OrthodonticsList)">正 畸</li>
      <li class="typein-header-first-level-sub" @click="tabChange(UploadPic)">图片路径与疾病大类</li>
    </ul>
    <div class="underline"></div>
    <div :is="currentView"></div>
    <div class="typein-content">
      <router-view></router-view>
    </div>
    <div class="button-to-homepage">
      <el-button type="primary" icon="el-icon-edit" @click="buttonToHomepage">个人主页</el-button>
    </div>
  </div>
</template>

<script>
  import SurgeonList from '../../sub/SubTypein/surgeonList';
  import OrthodonticsList from '../../sub/SubTypein/orthodonticsList';
  import UploadPic from '../../sub/SubTypein/UpLoadPic';

  export default {
    name: "typein",
    data() {
      return {
        SurgeonList: 'SurgeonList',
        OrthodonticsList: 'OrthodonticsList',
        UploadPic: 'UploadPic',
        currentView: 'SurgeonList',
        userId: 0,
      };
    },
    components: {
      SurgeonList,
      OrthodonticsList,
      UploadPic,
    },
    methods: {
      tabChange: function (tabItem) {
        this.currentView = tabItem;
        const personId = this.$route.query.personId;
        const personIdQuery = personId ? {personId} : {};
        if (tabItem === this.SurgeonList) {
          this.$router.push({path: `/typein/personalDetail`, query: personIdQuery});
        } else if (tabItem === this.OrthodonticsList) {
          this.$router.push({path: `/typein/caseHistory`, query: personIdQuery});
        } else if (tabItem === this.UploadPic) {
          this.$router.push({path: `/typein/upLoadPic`, query: personIdQuery});
        }
      },
      buttonToHomepage() {
        this.userId = this.$store.state.userInfo.userId;
        if (this.userId === -1) {
          alert("请重新登陆");
          this.$router.push({path: `/login`});
        } else {
          this.$router.push({path: `/homepage/${this.userId}/userInfo`});
        }

      }
    }
  }
</script>

<style lang="scss">
  .typein-wrap {
    background-color: #D1D1D1;
    position: relative;
    width: 800px;
    min-height: 70vh;
    max-height: 99vh;
    overflow: auto;
    border-radius: 5px;
    padding: 0.2rem 0.5rem;
    .typein-header-first-level {
      display: flex;
      flex-wrap: nowrap;
      justify-content: center;
      align-items: center;
      height: 2.1rem;
      font-size: 1.5rem;
      padding: 0.3rem 0;
      cursor: pointer;
      list-style-type: none;
      /*下划线跟随*/

      .typein-header-first-level-sub {
        flex: 1;
        text-align: center;
      }
    }
    .underline {
      border-bottom: 3px solid #10adff;
      position: absolute;
      bottom: 0;
      width: 0;
    }
    .typein-content {
      margin: 3rem;
    }
    .button-to-homepage {
      position: fixed;
      top: 1rem;
      right: 1rem;
    }
  }
</style>



