<template>
  <div>
    <el-row class="homepage-other-row">
      <el-button type="info" @click="changePWD">修改密码</el-button>
      <el-button type="info" v-if="adminIsShow" @click="changeAuthority">修改权限</el-button>
    </el-row>
    <el-row class="homepage-other-row">
      <el-button type="info" @click="logout">退出登录</el-button>
    </el-row>
    <el-row class="homepage-other-row">
      <el-button type="info"   v-if="adminIsShow" @click="setDiseaseListShow">设置疾病大类</el-button>
    </el-row>
    <el-row class="homepage-other-row">
      <el-button type="info" v-if="adminIsShow" @click="exportAsExcel">导出数据为excel</el-button>
    </el-row>
    <el-row class="homepage-other-row">
      <el-button type="info"  @click="setPathPre">设置通用路径</el-button>
      <el-button type="info"  @click="setPathDopphin">设置Dolphin路径</el-button>
    </el-row>
    <div class="change-pwd homepage-other-wrap" v-if="changePWDIsShow">
      <el-input class="homepage-other-single" :placeholder="username" v-model="username" :disabled="true"> </el-input>
      <el-input class="homepage-other-single" placeholder="请输入新密码" v-model="newPWD" clearable> </el-input>
      <el-button class="homepage-other-single" type="info" @click="changePWDUp">修改</el-button>
    </div>
    <div class="change-authority homepage-other-wrap" v-if="changeAuthorityIsShow">
      <el-input class="homepage-other-single" placeholder="请输入要修改的用户名" v-model="changeAuthorityUsername"> </el-input>
      <el-button class="homepage-other-single" @click="changeAuthorityTestUsername">检测该用户名</el-button>
      <div class="homepage-other-single" v-if="changeAuthorityConfirmIsShow">
        <el-select v-model="roleId" placeholder="请选择">
          <el-option
            v-for="item in authorities"
            :key="item.value"
            :label="item.label"
            :value="item.value"
            :disabled="item.disabled">
          </el-option>
        </el-select>
        <el-button class="homepage-other-single" type="info" @click="changeAuthorityUp">修改</el-button>
      </div>
    </div>
    <div class="set-disease homepage-other-wrap" v-if="setDiseaseListIsShow">
      <el-tag :key="disease.id"
        v-for="disease in diseaseList" closable :disable-transitions="false" @close="handleClose(disease.id)">
        {{disease.className}}
      </el-tag>
      <el-input
        class="input-new-tag" v-if="inputVisible" v-model="inputValue" ref="saveTagInput"
        size="small" @keyup.enter.native="handleInputConfirm" @blur="handleInputConfirm">
      </el-input>
      <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
    </div>
    <div class="set-path-pre homepage-other-wrap" v-if="setPathPreIsShow">
      <el-input  placeholder="请输入图片的前缀路径，类似D:\pics" v-model="pathPre"
                @keyup.enter.native="handlePathPreConfirm" @blur="handlePathPreConfirm"> </el-input>
    </div>
    <div class="set-dolphin-path homepage-other-wrap" v-if="setDolphinPathIsShow">
      <el-input  placeholder="请输入本机Dolphin的路径，类似D:\program\dolphin\DolphinViewer.exe" v-model="pathDolphin"
                @keyup.enter.native="handleDolphinPathConfirm" @blur="handleDolphinPathConfirm"> </el-input>
    </div>
  </div>
</template>

<script>
  import Global from '../../global/global';

  export default {
    name: "homepage-other",
    data() {
      return {
        username: "",
        //本用户的roleID，用于控制v-if显示
        roleBenRen: 2,
        newPWD: "",
        changeAuthorityUsername: "",
        authorities: [{
            value: 2,
            label: '游客'
          },
          {
            value: 1,
            label: '普通用户'
          },
          {
            value: 0,
            label: '管理员',
          }],
        userId: '',
        changePWDIsShow: false,
        changeAuthorityIsShow: false,
        changeAuthorityConfirmIsShow: false,
        setDiseaseListIsShow: false,
        adminIsShow: false,
        setPathPreIsShow: false,
        setDolphinPathIsShow: false,
        //疾病大类相关变量
        diseaseList:[],
        inputVisible: false,
        inputValue: '',
        //路径
        pathPre: '',
        pathDolphin: '',
      }
    },
    mounted(){
      this.username = this.$store.state.userInfo.username;
      //请求目前所有疾病大类数据
      this.$ajax({
        method: 'get',
        url: `http://${Global.serverIP}:${Global.port}/bigclass/searchAll`,
        headers: {
          'Content-Type': 'application/json',
        },
        xhrFields: {
          withCredentials: true
        },
      }).then(response=>{
        console.log(response);
        if(response.data.code === 1){
          this.diseaseList = response.data.content;
        }else if(response.data.code === 0){
          this.diseaseList = [{id:-1,
            className:'暂无疾病大类，请联系管理员添加'}];
        }
        console.log('diseaseList',this.diseaseList);
      });
      //localstorage取roleBenRen
      let cache = JSON.parse(localStorage.getItem('MedicineDatabaseUserInfo'));
      this.roleBenRen = cache.roleId;
      if(this.roleBenRen === 0){
        this.adminIsShow = true;
      }
    },
    methods: {
      changePWD() {
        this.changePWDIsShow = !this.changePWDIsShow;
        this.changeAuthorityIsShow = false;
        this.changeAuthorityConfirmIsShow = false;
        this.setDiseaseListIsShow = false;
        this.setPathPreIsShow = false;
        this.setDolphinPathIsShow = false;
        //this.username = this.$store.state.userInfo.username;
      },
      changePWDUp() {
        this.$ajax({
          method: 'post',
          url: `http://${Global.serverIP}:${Global.port}/changepwd?userName=${this.username}&newPassword=${this.newPWD}`,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields: {
            withCredentials: true
          },
        }).then((response) => {
          console.log("修改密码后的response");
          console.log(response);
          if (response.data.code === 1) {
            this.$notify({
              title: '成功',
              message: '密码修改成功',
              type: 'success',
            });
            this.changePWDIsShow = false;
          } else if (response.data.code === 0) {
            this.$notify.error({
              title: '成功',
              message: '密码修改失败',
            });
          }
          // else if(response.data.code === 2){
          //   this.$alert('登陆信息失效', '请重新登录', {
          //     confirmButtonText: '前往登陆',
          //     callback: () => {
          //       this.$router.push({path:`/login`});
          //     }
          //   });
          // }else if(response.data.code === 3){
          //   this.$confirm('权限不足', '请联系管理员修改权限', {
          //     confirmButtonText: '确定',
          //     cancelButtonText: '取消',
          //     type: 'warning'
          //   });
          // }
        });
      },
      changeAuthority() {
        console.log("改权限了");
        this.changeAuthorityIsShow = !this.changeAuthorityIsShow;
        this.changePWDIsShow = false;
        this.changeAuthorityConfirmIsShow = false;
        this.setDiseaseListIsShow = false;
        this.setPathPreIsShow = false;
        this.setDolphinPathIsShow =false;
      },
      changeAuthorityTestUsername() {
        if(this.changeAuthorityUsername === this.username){
          this.$message("禁止操作本用户",{});
          return;
        }else{
          this.$ajax({
            method: 'get',
            url: `http://${Global.serverIP}:${Global.port}/search/userInfo?userName=${this.changeAuthorityUsername}`,
            headers: {
              'Content-Type': 'application/json',
            },
            xhrFields: {
              withCredentials: true
            },
          }).then(response => {
            console.log("检查用户名的response：")
            console.log(response);
            if (response.data.code === 0) {
              this.$alert('找不到该用户','',  {
                confirmButtonText: '重新输入',
              })
            } else if (response.data.code === 1) {
              this.changeAuthorityConfirmIsShow = true;
              this.roleId = response.data.content.roleId;
              this.userId = response.data.content.userId;
            }
          });
        }
      },
      changeAuthorityUp(){
        console.log(this.roleId);
        console.log(this.userId);
        console.log(`http://${Global.serverIP}:${Global.port}/changestatus?targetStatus=${this.roleId}&userId=${this.userId}`)
        this.$ajax({
          method: 'get',
          url: `http://${Global.serverIP}:${Global.port}/changestatus?targetStatus=${this.roleId}&userId=${this.userId}`,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields: {
            withCredentials: true
          },
        }).then(response=>{
          console.log("修改完权限的返回：");
          console.log(response);
          if(response.data.code === 1){
            console.log("targetStatus是"+this.roleId);
            this.$alert('修改成功','修改用户权限',  {
              confirmButtonText: '确定',
            });
          }else if(response.data.code === 0){
            this.$alert(`${response.data.msg}`,'修改用户权限',  {
              confirmButtonText: '确定',
            });
          }
        })
      },
      logout(){
        console.log("退出登录");
        this.$confirm('是否确认退出登录？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$ajax({
            method: 'get',
            url: `http://${Global.serverIP}:${Global.port}/logout`,
            headers: {
              'Content-Type': 'application/json',
            },
            xhrFields: {
              withCredentials: true
            },
          }).then(response=>{
            if(response.data.code === 1){
              localStorage.removeItem('MedicineDatabaseUserInfo');
              this.$router.push({path: `/login`});
            }else if(response.data.code === 0){
              console.warn("退出登陆失败");
            }
          })
        });
      },
      /**
       *       setDisease
       */
      setDiseaseListShow(){
        this.setDiseaseListIsShow = !this.setDiseaseListIsShow;
        this.changePWDIsShow = false;
        this.changeAuthorityIsShow = false;
        this.changeAuthorityConfirmIsShow = false;
        this.setPathPreIsShow = false;
        this.setDolphinPathIsShow = false;
      },
      //删除标签
      handleClose(id) {
        this.$ajax({
          method: 'get',
          url: `http://${Global.serverIP}:${Global.port}/bigclass/deleteOne?classId=${id}`,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields: {
            withCredentials: true
          },
        }).then(response=>{
          if(response.data.code === 1){
            this.diseaseList.splice(this.diseaseList.findIndex(value => value.id === id), 1);
          }
      })
      },
      showInput() {
        this.inputVisible = true;
        this.$nextTick(() => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
      },
      handleInputConfirm() {
        let inputValue = this.inputValue;
        //如果添加了疾病大类，开始执行添加操作的ajax
        if (inputValue) {
          let formData = {
            className: inputValue
          };
          this.$ajax({
            method: 'post',
            url: `http://${Global.serverIP}:${Global.port}/bigclass/addOne`,
            headers: {
              'Content-Type': 'application/json',
            },
            xhrFields: {
              withCredentials: true
            },
            data: JSON.stringify(formData),
          }).then(response=>{
            //添加完成后重新获取当前存在的所有疾病大类
              console.log("添加了疾病大类的response：",response);
            //请求目前所有疾病大类数据
            this.$ajax({
              method: 'get',
              url: `http://${Global.serverIP}:${Global.port}/bigclass/searchAll`,
              headers: {
                'Content-Type': 'application/json',
              },
              xhrFields: {
                withCredentials: true
              },
            }).then(response=>{
              console.log(response);
              if(response.data.code === 1){
                this.diseaseList = response.data.content;
              }else if(response.data.code === 0){
                this.diseaseList = [{id:-1,
                  className:'暂无疾病大类，请联系管理员添加'}];
              }
              console.log('diseaseList:',this.diseaseList);
            })
          });
        }
        this.inputVisible = false;
        this.inputValue = '';
      },
      exportAsExcel(){
        window.open(Global.exportServerIpAddress, '_blank').location;
      },
      setPathPre(){
        this.changePWDIsShow = false;
        this.changeAuthorityIsShow = false;
        this.changeAuthorityConfirmIsShow = false;
        this.setDiseaseListIsShow = false;
        this.setPathPreIsShow = !this.setPathPreIsShow;
        this.setDolphinPathIsShow = false;
        if(localStorage.getItem('pathPre')){
          this.pathPre = localStorage.getItem('pathPre');
        }
      },
      setPathDopphin(){
        this.changePWDIsShow = false;
        this.changeAuthorityIsShow = false;
        this.changeAuthorityConfirmIsShow = false;
        this.setDiseaseListIsShow = false;
        this.setPathPreIsShow = false;
        this.setDolphinPathIsShow = !this.setDolphinPathIsShow ;
        if(localStorage.getItem('pathDolphin')){
          this.pathDolphin = localStorage.getItem('pathDolphin');
        }
      },
      handlePathPreConfirm(){
        localStorage.setItem('pathPre', this.pathPre);
      },
      handleDolphinPathConfirm(){
        localStorage.setItem('pathDolphin', this.pathDolphin);
      },

    }
  }
</script>

<style scoped>
  .homepage-other-row{
    margin-bottom: 0.5rem;
  }
  .homepage-other-wrap{
    margin-top: 0.5rem;
  }
  .homepage-other-single {
    margin-bottom: 0.5rem;
  }
  .el-tag + .el-tag {
    margin-left: 10px;
  }
  .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
</style>



