<template>
  <div class="homepage-database-wrap">
    <div class="homepage-database-header">
      <el-radio-group v-model="checkDisease" @change="checkDiseaseChanged">
        <el-radio class="homepage-database-header-single" :label="'diseaseAll'">全部</el-radio>
        <el-radio class="homepage-database-header-single" v-for="disease in diseaseList" :label="disease.id" :key="disease.id">{{disease.className}}</el-radio>
      </el-radio-group>
    </div>
    <div class="homepage-database-list">
      <el-table :data="thisPageData" stripe style="width: 100%" height="70vh">
        <el-table-column prop="personName" label="姓名" width="180"> </el-table-column>
        <el-table-column prop="personSex" label="性别" width="180"> </el-table-column>
        <el-table-column prop="patientId" label="病人Id"> </el-table-column>
        <el-table-column label="操作" >
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        class="pagination"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="10"
        layout="prev, pager, next, jumper"
        :total="thisDiseaseTotalNum">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import Global from '../../global/global';
    export default {
        name: "homepage-database",
      data(){
          return{
            personId: '',
            roleId: 2,
            adminIsShow: false,
            diseaseList: [],
            checkDisease: 0,
            //主题数据列表参数
            thisPagePersonIdList: [],
            thisPageData: [],
            thisPageNumber: 1,
            total: 0,
            currentPage: 1,
            thisDiseaseTotalNum: 0,
          }
      },
      created(){
      },
      mounted(){
        //获取所有疾病大类存入diseaseList
        this.$ajax({
          method: 'get',
          url: `http://${Global.serverIP}:${Global.port}/bigclass/searchAll`,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields: {
            withCredentials: true
          },
        }).then(response=>{
          if(response.data.code === 1){
            this.diseaseList = response.data.content;
          }else if(response.data.code === 0){
            this.diseaseList = [{id:-1,
                                  className:'暂无疾病大类，请联系管理员添加'}];
          }
          console.log('diseaseList',this.diseaseList);
        });
        //localstorage取roleId
        // let cache = JSON.parse(localStorage.getItem('MedicineDatabaseUserInfo'));
        // this.roleId = cache.roleId;
        // if(this.roleId === 0){
        //   this.adminIsShow = true;
        // }
      },
      methods: {
        checkDiseaseChanged() {
          console.log('checkDisease', this.checkDisease);
          if(this.checkDisease !== 'diseaseAll'){
            //根据疾病id获取personId的数组
            this.$ajax({
              method: 'get',
              url: `http://${Global.serverIP}:${Global.port}/waike/search/personIds?classId=${this.checkDisease}&pageSize=10&pageNum=1`,
              headers: {
                'Content-Type': 'application/json',
              },
              xhrFields: {
                withCredentials: true
              },
            }).then(response => {
              console.log("根据疾病id获取personid的response", response);
              if(response.data.code === 1){
                this.thisPagePersonIdList = response.data.content.personIdArray;
                this.thisDiseaseTotalNum = response.data.content.totalNum;
                this.getDetailByPersonIdList(this.thisPagePersonIdList);
                this.$store.commit("setThisPageData", this.thisPageData);
              }else if(response.data.code === 0){
                this.thisPagePersonIdList = [];
                this.thisDiseaseTotalNum = 0;
                this.thisPageData = [];
              }
            });
          }else{
            //获取所有病例数据第一页
            this.$ajax({
              method: 'get',
              url: `http://${Global.serverIP}:${Global.port}/search/patients?pageSize=10&pageNum=1`,
              headers: {
                'Content-Type': 'application/json',
              },
              xhrFields: {
                withCredentials: true
              },
            }).then(response => {
              console.log("获取personid的response", response);
              if(response.data.code === 1){
                this.thisPageData = response.data.content.personIdArray;
                this.thisDiseaseTotalNum = response.data.content.totalNum;
                this.$store.commit("setThisPageData", this.thisPageData);
              }else if(response.data.code === 0){
                this.thisPagePersonIdList = [];
                this.thisDiseaseTotalNum = 0;
                this.thisPageData = [];
              }
            });
          }

        },
        handleCurrentChange(newPage) {
          if(this.checkDisease !== 'diseaseAll'){
            //分页查询classId下的第newPage页数据
            this.$ajax({
              method: 'get',
              url: `http://${Global.serverIP}:${Global.port}/waike/search/personIds?classId=${this.checkDisease}&pageSize=10&pageNum=${newPage}`,
              headers: {
                'Content-Type': 'application/json',
              }
            }).then((response) => {
              if (response.data.code === 1) {
                console.log("第",newPage,"页的response",response);
                this.thisPagePersonIdList = response.data.content.personIdArray;
                this.getDetailByPersonIdList(this.thisPagePersonIdList);
                this.$store.commit("setThisPageData", this.thisPageData);
                this.currentPage = newPage;
              }
            });
          }else{
            //分页查询所有病例信息第NewPage页
            this.$ajax({
              method: 'get',
              url: `http://${Global.serverIP}:${Global.port}/search/patients?pageSize=10&pageNum=${newPage}`,
              headers: {
                'Content-Type': 'application/json',
              },
              xhrFields: {
                withCredentials: true
              },
            }).then(response => {
              if(response.data.code === 1){
                this.thisPageData = response.data.content.personIdArray;
                this.$store.commit("setThisPageData", this.thisPageData);
                this.currentPage = newPage;
              }else if(response.data.code === 0){
                this.thisPagePersonIdList = [];
                this.thisDiseaseTotalNum = 0;
                this.thisPageData = [];
              }
            });
          }
        },
        // rowClick(row, event, column) {
        //   this.$router.push({path: `/personInfo/${row.personId}`});
        // },
        handleEdit(index,row){
          this.$router.push({path:`/personInfo/${row.personId}`});
        },
        handleDelete(index,row){
          this.$confirm("确认删除操作吗","提示",{
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(()=>{
            //删除该病例的请求。
            this.$ajax({
              method: 'get',
              url: `http://${Global.serverIP}:${Global.port}/waike/deleteOne?personId=${row.personId}`,
              headers: {
                'Content-Type': 'application/json',
              }
            }).then(response=>{
              if(response.data.code === 1){
                this.$message("删除成功");
                this.handleCurrentChange(this.currentPage);
              }else if(response.data.code === 0 ){
                this.$message(response.data.msg);
              }
            })
          });
        },
        /**
         * 封装一个根据personId数组获取这页缩略信息的函数
         */
        getDetailByPersonIdList(personIdList){
          this.$ajax({
            method: 'post',
            url: `http://${Global.serverIP}:${Global.port}/search/personInfos`,
            headers: {
              'Content-Type': 'application/json',
            },
            xhrFields: {
              withCredentials: true
            },
            data: JSON.stringify(personIdList),
          }).then(response => {
            console.log("getDetailByPersonIdList函数输出的：",response);
            if(response.data.code === 1) {
              this.thisPageData = response.data.content;
            }
          });
        }
      }
      }
</script>

<style scoped lang="scss">
  .homepage-database-header-single{
    margin: 0.1rem;
  }
  .homepage-database-list{
    margin-top: 1rem;
  }
</style>



