<template>
  <div class="person-detail-wrap">
    <input-box :title="tagTitleFirst" id="treatHistory" v-model="postData.treatHistory"> </input-box>
    <input-box :title="tagTitleSecond" id="nasopharyngealDisease" v-model="postData.nasopharyngealDisease"> </input-box>
    <input-box :title="tagTitleThird" id="feedPattern" v-model="postData.feedPattern"> </input-box>
    <input-box :title="tagTitleFourth" id="badHadit" v-model="postData.badHadit"> </input-box>
    <input-box :title="tagTitleFifth" id="familyIsTreat" v-model="postData.familyIsTreat"> </input-box>
    <div class="submit-button" @click="_submit" >提交，进入下一页</div>
  </div>
</template>

<script>
  import InputBox from '../../../assets/inputBox/inputBox'
  import Global from '../../../global/global';
  export default {
    name: "case-history",
    data(){
      return{
        tagTitleFirst: "正畸治疗史",
        tagTitleSecond: "鼻咽部疾病",
        tagTitleThird: "喂养方式",
        tagTitleFourth: "不良习惯",
        tagTitleFifth: "家属是否有正畸治疗史",
        postData: {
          treatHistory:'',
          nasopharyngealDisease:'',
          feedPattern:'',
          badHadit:'',
          familyIsTreat:'',
        },
        personId: '',
      };
    },
    components:{
      InputBox,
    },
    created() {
      if (this.$route.query.personId) {
        this.personId = this.$route.query.personId;
      } else {
        alert("请先输入该病例个人信息数据");
        this.$router.push({path: `/typein/personalDetail`});
      }
    },
    methods:{
      _submit(event) {
        event.preventDefault();
        const formData = JSON.stringify(this.postData);
        this.$ajax({
          method: 'post',
          url: `http://${Global.serverIP}:${Global.port}/zhenji/add/${this.personId}/zjkMedicalHistory`,
          data: formData,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields:{
            withCredentials:true
          },
        }).then((response) => {
          console.log(response);
          this.$router.push({path:`/typein/examination?personId=${this.personId}`});
        })
      }
    }
  }
</script>


<style scoped lang="scss">
  @import "../../../scss/style";

  .submit-button{
    @extend %submit-button;
  }
</style>



