<template>
  <div class="login">
    <div class="login-title"><p>医学信号数据库系统</p></div>
    <div class="login-wrap">
      <div class="login-header">登 陆</div>
      <div class="login-body">
        <form>
          <div class="form-group">
            <label>用户名：</label>
            <input type="text" class="form-control" name="username" ref="username" required="true" placeholder="请输入您的用户名" />
          </div>
          <div class="form-group">
            <label>密码：</label>
            <input type="password" class="form-control" name="password" ref="password" required="true" placeholder="请输入您的密码" />
          </div>
          <div class="form-group clearfix">
            <button class="submit-login" @click="_login">登陆</button>
          </div>
        </form>
        <div class="login-body-bottom">
          <router-link to="/register">
          <div class="login-body-register"><a>还没有账号，注册</a></div>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Global from '../../global/global';

  export default {

    name: "login",
    data(){
      return {
        username: "",
        password: "",
        roleId: 2,
      }
    },
    methods:{
      _login:function(event){
        event.preventDefault();
        let that = this;
        that.username = that.$refs.username.value;
        that.password = that.$refs.password.value;
        this.$ajax({
          method: 'post',
          url: 'http://' + Global.serverIP + ':' + Global.port + '/login',
          data: {
            userName: that.username,
            passWord: that.password
          },
          headers:{
            'Content-Type':'application/json',
          },
          xhrFields:{
            withCredentials:true
          },
        }).then((response)=>{
          console.log("下面是登陆的response");
          console.log(response);
            if(response.data.code === 1){
              const userInfo = {
                userId: response.data.userId,
                username: response.data.username,
                recordedNumber: response.data.count,
                roleId: response.data.roleId,
              };
              localStorage.setItem('MedicineDatabaseUserInfo', JSON.stringify(userInfo));
              this.$store.commit("setUserInfo",userInfo);
              this.$router.push({path:`/homepage/${response.data.userId}`});

            }else if(response.data.code === 0){
              alert("用户名或密码错误！");
            }
        })
      }
    }
  }
</script>

<style lang="scss">
  @import "../../scss/font";
  @import "../../scss/style";


  .login {
    position: fixed;
    min-height: 352px;
    min-width: 352px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    background-color: #E8E8E8;
    -webkit-border-radius: $border-radius-big;
    -moz-border-radius: $border-radius-big;
    border-radius: $border-radius-big;
    .login-title {
      margin: $box-padding auto 10px;
      p {
        font-size: $font-size-title;
        font-weight: bold;
        line-height: $font-size-title;
        text-align: center;
      }
    }
    .login-wrap {
      padding: $box-padding;
      .login-header{
        font-size: $font-size-subtitle;
        margin-bottom: 10px;
        font-weight: bold;
      }
      .login-body{
        font-size:$font-size-normal;
        form{
          .form-group{
            margin-bottom: 16px;
            label{
              display: inline-block;
              max-width: 100%;
              margin-bottom: 8px;
            }
            input{
              @extend %input;
            }
            .submit-login{
              float: right;
              @extend %submit-button;
            }
          }
        }
        .login-body-bottom{
          display: flex;
          justify-content: flex-end;
          .login-body-register{
            cursor: pointer;
            a{
              text-decoration: none;
              color: black;
              &:hover {
               color: red;
               text-decoration: underline;
              }
            }
          }
        }
      }
    }
  }

</style>



